.bg-scale{
    position: fixed;
    display: none;
    left: 0px;
    right: 0px;
    top: 0px;
    margin: 0px auto;
    height: 100vh;
    z-index: 1;
    width: 100%;
    opacity: 0.6;
    }
.bg-scale.on{
    display:block;
    }
.row{
    height: 100vh;
    padding: 0px;
    margin-right: calc(-.26 * var(--bs-gutter-x)) !important;
    margin-left: calc(-.26 * var(--bs-gutter-x)) !important;
}

.column{
    position: relative;
    display: block;
    height: 100%;
    padding: 0px 5.5px;
    }
.column::after{
    content: "";
    background-color: #bbb7ac;
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
}
